import React from 'react';
import { CatalogItem } from './CatalogItem';
import c from './Catalog.module.scss';
import { useNavigate } from 'react-router-dom';
export const Catalog = ({ mobile = false }) => {
  const navigate = useNavigate();

  return (
    <div className={c.catalog}>
      {!mobile && (
        <div className={c.catalogMain}>
          <div onClick={() => navigate('/catalog')}></div>
        </div>
      )}
      <CatalogItem mobile={mobile} title=" " />
      <CatalogItem mobile={mobile} title=" " />
      <CatalogItem mobile={mobile} title="" />
      <CatalogItem mobile={mobile} title="  DOR" />
      <CatalogItem mobile={mobile} title="-" />
      <CatalogItem mobile={mobile} title="" />
      <CatalogItem mobile={mobile} title="" />
      <CatalogItem mobile={mobile} title="   " />
      <CatalogItem mobile={mobile} title="  " />
      <CatalogItem mobile={mobile} title="  " last={true} />
    </div>
  );
};
